<template>
<div id="component2">
        id <input type=number v-model="id">
        <button v-on:click="callMethod()">{{value}}</button>
        <p>{{student.name}}{{student.mark}}</p>
    </div>
</template>
<script>
import {mapActions} from 'vuex';
import {mapGetters} from 'vuex';

export default {
    props:['value'],
    data(){
        return {id:'',
                student:{
                    id:'',
                    name:'',
                    mark:''
                }
                }
    },
    methods: {
        ...mapGetters([
            'getFilteredStudent'
        ]),
        ...mapActions([
            'deleteStudent',
            'getStudentById'
        ]),
        callMethod(){
            if(this.value=='get')
            {
                this.getStudentById(this.id);
                this.student=this.getFilteredStudent();
            }
            else
                this.deleteStudent(this.id);
        }
    }
}
</script>

<style scoped>
#product-list-one{
    background: #FFF8B1;
    box-shadow: 1px 2px 3px rgba(0,0,0,0.2);
    margin-bottom: 30px;
    padding: 10px 20px;
}
#product-list-one ul{
    padding: 0;
}
#product-list-one li{
    display: inline-block;
    margin-right: 10px;
    margin-top: 10px;
    padding: 20px;
    background: rgba(255,255,255,0.7);
}
.price{
    font-weight: bold;
    color: #E8800C;
}
</style>



